import React from 'react'
import { Link } from 'react-router-dom'
 import './Home.css'
 import HomePicture from './assets/images/Cost-Calculator.jpg'


const Home = () => {
    return (
       
        <React.Fragment>
        
        
        <p className='expenseCalculator'>EXPENSE CALCULATOR</p>
        <div className="paragraph">
        <h4 className='paragraph1'>See where your money goes</h4>
        <h4 className='paragraph2'>Simple App to start Saving More</h4>
        </div>
        <img src={HomePicture} alt="Logo" />
        <div className="buttons3">

              <p> If you don't have account please 
                <Link to='/register'>
                <button className="main-buttons3" id="products-btn3">REGISTER</button></Link></p>
                <p> Or 
                <Link to='/'>
                <button className="main-buttons3" id="expenses-btn3">LOG IN</button>
                </Link></p>
</div>
        </React.Fragment>


    )
}

export default Home